@font-face {
    font-family: 'Conv_Panton-SemiBold';
    src: url('fonts/Panton-SemiBold.eot');
    src: local('☺'), url('fonts/Panton-SemiBold.woff') format('woff'), url('fonts/Panton-SemiBold.ttf') format('truetype'), url('fonts/Panton-SemiBold.svg') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Proxima Nova Lt';
    src: url('fonts/ProximaNova-Semibold.eot');
    src: local('Proxima Nova Semibold'), local('ProximaNova-Semibold'),
        url('fonts/ProximaNova-Semibold.eot?#iefix') format('embedded-opentype'),
        url('fonts/ProximaNova-Semibold.woff') format('woff'),
        url('fonts/ProximaNova-Semibold.ttf') format('truetype');
    font-weight: 600;
    font-style: normal;
}

@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');

@font-face {
    font-family: 'Proxima Nova Lt';
    src: url('fonts/ProximaNova-Light.eot');
    src: local('Proxima Nova Light'), local('ProximaNova-Light'),
        url('fonts/ProximaNova-Light.eot?#iefix') format('embedded-opentype'),
        url('fonts/ProximaNova-Light.woff') format('woff'),
        url('fonts/ProximaNova-Light.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
}

@media screen and (max-width:576px) {
    .ah2 {
        font-size: 20px !important;
        margin: 5px 0 !important;
        padding: 5px 0 !important;
    }
}

.main-banner-block {
    padding-top: 12px;
    padding-bottom: 20px;
    font-family: 'Montserrat', sans-serif;
    background: center / cover url(../images/img1.WebP) no-repeat;

    .ah1 {
        margin-bottom: 22px;
        font-style: normal;
        font-size: 38px;
        line-height: 42px;
        font-weight: 900;

        @media screen and (max-width:576px) {
            font-size: 25px;
            line-height: 28px;
        }
    }

    .ah2 {
        margin-bottom: 22px;
        font-size: 18px;
        line-height: 22px;
        font-weight: bold;
        letter-spacing: 1px;
    }

    .ah3 {
        padding-right: 40px;
        font-size: 14px;
        line-height: 17px;
        font-weight: 300;
        letter-spacing: 1px;
    }

    .ah4 {
        margin-top: 10px;
        font-size: 11px;
        line-height: 14px;
        font-weight: 500;
    }

    &-input {
        width: 100% !important;
        height: 40px;
        font-size: 16px;
        font-weight: bold;
        font-family: 'Circe', sans-serif;
        background-color: transparent;
        border: 2px solid #000;
        border-radius: 68px;
        color: #000;
        box-shadow: none;

        &::-webkit-input-placeholder {
            color: #000000;
        }

        &::-moz-placeholder {
            color: #000000;
        }

        /* Firefox 19+ */
        &:-moz-placeholder {
            color: #000000;
        }

        /* Firefox 18- */
        &:-ms-input-placeholder {
            color: #000000;
        }

        &:focus {
            box-shadow: none;
            border-color: #000;
        }

        @media screen and (min-width:1200px) {
            width: 50% !important;
        }

        @media screen and (max-width:1200px) {
            margin-right: 0 !important;
        }

        &:focus {
            color: #000;
        }
    }

    .orange-button {
        width: 100%;

        @media screen and (min-width:1200px) {
            width: 230px;
        }
    }

    &-image {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 100%;
            display: block;

            @media screen and (min-width:1200px) {
                width: 500px;
                margin-bottom: -30%;
            }
        }
    }

    &-form {
        .ah2 {
            margin-bottom: 11px;
        }
    }

    .white-block-second {
        margin-top: 20px;
        text-align: center;
    }
}

.orange-button {
    position: relative;
    justify-content: center;
    align-items: center;
    display: flex;
    font-size: 18px;
    font-weight: bold;
    font-family: 'Circe', sans-serif;
    color: #000 !important;
    background-color: #ffbd57 !important;
    border: 2px solid #ffbd57 !important;
    border-radius: 68px;
    box-shadow: none !important;
    box-sizing: border-box;
    transition: all 0.5s;
    cursor: pointer;

    &:before {
        content: "";
        left: 10px;
        width: 20px;
        height: 20px;
        margin-right: 17px;
        background-position: center;
        background-size: cover;
        background-image: url(../img/call-answer.svg);
    }

    &:hover {
        background-color: #fff !important;
        border: 2px solid #0d96ff !important;

        &:before {
            background-image: url(../img/call-answer-blue.svg);
        }
    }
}

.white-block {
    padding: 36px 28px;
    background-color: #fff;
}

.advantages-block {
    .row {
        justify-content: center;

        @media screen and (max-width:576px) {
            padding: 0px 15px;
        }
    }

    padding: 55px 0px 5px 0px;

    .ah2 {
        display: block;
        margin-bottom: 60px;
        text-align: center;
        font-size: 34px;
        line-height: 1.3;
        font-weight: 400;
        font-family: 'Montserrat', sans-serif;
    }

    &-item {
        @media screen and (min-width:1200px) {
            flex: 1 1 calc(25% - 20px);
        }

        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        margin: 0px 5px;
        padding: 50px 10px 36px 10px;
        background-color: #edfbfb;
        img {
            width: 75px;
        }

        p {
            text-align: center;
            margin-top: 34px;
            font-size: 18px;
            line-height: 22px;
            font-family: 'Futura PT Book', sans-serif;
        }

        @media screen and (max-width:992px) {
            margin-top: 10px;
        }
    }
}

.description-block {
    .media-body {
        flex: auto;
    }

    .container {
        border: 1px solid #0e96ff;
    }

    &-body {
        font-size: 16px;
        line-height: 20px;
        font-weight: 300;
        font-family: 'Montserrat', sans-serif;

        h5 {
            font-weight: bold;
            padding-bottom: 10px;
        }

        @media screen and (min-width:1200px) {
            padding: 57px 46px;
        }

        &-p0 {
            padding-top: 0;
        }
    }

    @media screen and (max-width:1200px) {
        .description-block-body {
            padding: 20px 15px;
        }

        img {
            width: 40%;
            align-self: center;
        }
    }

    @media screen and (max-width:576px) {
        .media {
            flex-direction: column;
        }

        img {
            margin-top: 10px;
            margin-bottom: 10px;
        }
    }
}

.brand-block {
    padding: 75px 75px;
    padding-bottom: 0;

    @media screen and (max-width:576px) {
        padding: 0;
    }

    .container {
        padding: 0;
    }

    .ah2 {
        display: block;
        text-align: center;
        font-size: 34px;
        line-height: 1.3;
        font-weight: 500;
        font-family: 'Montserrat', sans-serif;
    }

    .swiper-container {
        margin-top: 38px;
        padding-top: 20px;
        padding-bottom: 20px;

        @media screen and (max-width:992px) {
            margin-top: 0;
        }

        .swiper-slide {
            height: 110px;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.5s;

            @media screen and (max-width:768px) {
                height: 70px;
            }

            &:hover {
                background-color: #edfbfb;
                box-shadow: 0px 0px 24px 2px rgba(13, 150, 255, 0.2);
            }
        }

        .swiper-button-next,
        .swiper-button-prev {
            width: 46px;
            height: 46px;
            font-weight: bold;
            color: #000;
            cursor: pointer;
            background-size: 12px;
            filter: grayscale(1);

            &:hover {
                background-color: #edfbfb;
                color: #0e96ff;
                filter: grayscale(0);
            }

            &:after {
                font-size: 19px;
            }

            @media screen and (max-width:576px) {
                //display: none;
            }
        }
    }
}

.table-price-block {
    .price-row {
        background-color: #fff;
    }

    @media screen and (max-width:576px) {

        th,
        td {
            font-size: 12px !important;
        }

        th:nth-child(1) {
            padding-left: 10px !important;
        }

        a.show-btn {
            font-size: 11px !important;
            padding-bottom: 10px;
        }

        span:not(.ah2) {
            font-size: 11px !important;
        }

        .d-flex {
            padding-left: 0 !important;
        }

        td:nth-child(3) {
            display: none;
        }

        thead {
            th {
                &:nth-child(3) {
                    display: none;
                }
            }
        }

        .mobile-btn {
            width: 100%;
            display: table-row;
            background-color: #0e96ff;

            td {
                width: 100%;
            }

            a {
                color: #fff;
                text-align: center;
            }
        }
    }

    padding-top: 80px;

    @media screen and (max-width:992px) {
        padding-top: 0;
    }

    font-family: 'Montserrat',
    sans-serif;

    .ah2 {
        display: block;
        text-align: center;
        padding-bottom: 35px;
        font-size: 34px;
        line-height: 38px;
        font-weight: 500;
    }

    tr {
        background-color: #f6fdfd;
    }

    thead {
        tr {
            background-color: #0e96ff;

            th {
                vertical-align: middle;
                text-align: center;
                font-size: 14px;
                color: #fff;

                &:nth-child(1) {
                    text-align: left;
                }
            }
        }
    }

    th {
        font-size: 14px;
        font-family: 'Conv_Panton-SemiBold', sans-serif;
        color: #858585;

        &:nth-child(1) {
            padding-left: 30px;
        }
    }

    td {
        vertical-align: middle;
        text-align: center;
        font-size: 14px;

        &:nth-child(2) {
            font-size: 18px;
            font-weight: bold;
            font-family: 'Proxima Nova Lt', sans-serif;
            color: #000;
        }
    }

    td,
    th {
        font-weight: 400;
        border-top-width: 3px;
        border-top-color: #fff;

        &:nth-child(2) {
            width: 155px;
        }

        &:nth-child(3) {
            width: 120px;
        }
    }

    a {
        width: 100%;
        height: 100%;
        display: block;
        font-size: 14px;
        font-family: 'Conv_Panton-SemiBold', sans-serif;
        text-decoration: none;
        transition: all 0.5s;
    }

    td:nth-child(3):hover {
        background-color: #0e96ff;
        transition: all 0.5s;
        cursor: pointer;

        a {
            color: #fff;
            text-decoration: none;
        }
    }

    .d-flex {
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: center;
        padding-left: 20px;

        * {
            width: auto;
        }

        span {
            text-align: left;
            font-size: 13px;
            font-family: 'Proxima Nova Lt', sans-serif;
            color: #9b9b9b;
        }

        a {
            &:nth-child(1) {
                position: relative;
                margin-right: auto;
                font-size: 14px;
                letter-spacing: 1px;
                font-family: 'Roboto', sans-serif;
                cursor: pointer;
                color: #ff9c00;

                text-decoration: underline;
                @media screen and (max-width:992px) {
                    width: 100%;
                    margin-bottom: 20px;
                    text-align: left;
                }

                &:before {
                    content: "";
                    width: 27px;
                    height: 15px;
                    display: inline-block;
                    vertical-align: middle;
                    margin-right: 20px;
                    background: center/ cover url(../img/eyes.svg) no-repeat;
                }

                &:hover {
                    text-decoration: none;
                }
            }

            &:nth-child(3) {
                margin-left: 10px;
                margin-right: 35px;
            }

            @media screen and (max-width:576px) {
                margin: 0 !important;

                img {
                    width: 50px;
                }
            }

            @media screen and (max-width:380px) {
                img {
                    width: 30px;
                }
            }
        }
    }
}

.card-block {
    margin-top: 80px;
    font-family: 'Montserrat', sans-serif;

    .ah2 {
        display: block;
        text-align: center;
        padding-bottom: 45px;
        font-size: 34px;
        line-height: 38px;
        font-weight: 500;
        letter-spacing: 1px;
    }

    @media screen and (min-width:992px) {
        .col-12 {
            width: 20%;
            padding: 0;
            flex-basis: auto;
        }
    }

    .card-item {
        height: 140px;
        margin: 2px;
        padding: 30px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #fff;
        box-shadow: 0px 0px 24px 2px rgba(13, 150, 255, 0.2);
        transition: all 0.5s;

        &:hover {
            background-color: #0e96ff;
            box-shadow: none;
        }

        span {
            text-align: center;
            font-weight: bold;
            font-family: 'Proxima Nova Lt', sans-serif;
        }

        img {
            margin-bottom: 20px;
        }
    }
}

.wark-block {
    padding-bottom: 70px;
    font-family: 'Montserrat', sans-serif;

    .ah2 {
        display: block;
        text-align: center;
        padding-top: 80px;
        padding-bottom: 12px;
        font-size: 34px;
        line-height: 38px;
        font-weight: 500;
        letter-spacing: 1px;

        @media screen and (max-width:576px) {
            .ah2 {
                padding-top: 0;
            }
        }
    }

    &-item {
        position: relative;
        height: 263px;
        flex: 1 1 calc(20% - 20px);
        text-align: center;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 40px 10px;
        box-sizing: border-box;
        transition: all 0.5s;

        >div {
            height: 50px;
        }

        p {
            margin-top: 30px;
            margin-bottom: 30px;
            text-align: center;
            font-size: 20px;
            line-height: 24px;
            font-weight: 600;
            font-family: 'Proxima Nova Lt', sans-serif;
        }

        span {
            text-align: center;
            font-size: 16px;
            line-height: 19px;
            font-weight: 300;
            letter-spacing: 1px;
            font-family: 'Proxima Nova Lt', sans-serif;
        }

        &:hover {
            background-color: #edfbfb;
            box-shadow: 0px 0px 24px 2px rgba(13, 150, 255, 0.2);
        }

        &:after {
            content: "";
            position: absolute;
            right: -20px;
            top: 50%;
            margin-top: -40px;
            width: 47px;
            height: 53px;
            display: block;
            background: center / cover url(../img/keyboard-right-arrow-button2.svg) no-repeat;

            @media screen and (max-width:600px) {
                display: none;
            }

            @media screen and (max-width:1050px) {
                right: 0;
            }
        }
    }

    .col-12 {
        padding: 0;

        &:last-child {
            .wark-block-item {
                &:after {
                    display: none;
                }
            }
        }
    }
}

.form-block {
    .container {
        @media screen and (min-width:1200px) {
            padding-left: 0px;
            padding-right: 0px;
        }

        .row {
            .col-12 {
                &:nth-child(1) {
                    padding-right: 0px;

                    @media screen and (max-width:992px) {
                        padding-right: 15px;
                    }
                }

                &:nth-child(2) {
                    padding-left: 0px;

                    @media screen and (max-width:992px) {
                        padding-left: 15px;
                    }
                }
            }
        }
    }

    .ah2 {
        display: block;
        text-align: center;
        margin-bottom: 35px;
        font-size: 26px;
        line-height: 29px;
        font-weight: bold;
        font-family: 'Montserrat', sans-serif;
        color: #fff;
    }

    .form-group {
        margin-left: 0;
        margin-right: 0;
        justify-content: center;

        input {
            height: 40px;
            font-size: 16px;
            font-weight: bold;
            font-family: 'Circe', sans-serif;
            color: #fff;
            background-color: transparent;
            border: 2px solid #fff;
            border-radius: 20px;
            line-height: 1;
            text-align: center;
            width: 216px;
            max-width: 100%;
            margin: 0 auto;

            &::placeholder {
                color: #fff;
            }
        }

        button {
            width: 216px;
            max-width: 100%;
            margin: 0 auto;
            margin-bottom: 10px;
        }
    }

    p {
        font-family: 'Montserrat', sans-serif;
        text-align: center;
        font-size: 11px;
        line-height: 15px;
        color: #fff;
        width: 216px;
        max-width: calc(100% - 40px);
        margin: 0 auto;
    }

    &-right {
        padding: 55px 0px;
        background-color: #0e96ff;

        @media screen and (max-width:576px) {
            padding: 20px;
        }
    }

    .d-flex {
        height: 100%;
        align-items: center;

        img {
            width: 100%;
            display: block;
        }
    }
}



@media screen and (min-width:993px) {

}

@media screen and (max-width:575px) {
    .main-banner-block {
        background: none !important;
    }

    .main-banner-block .white-block-second {
        margin-top: 0;
        padding-top: 0;
        padding-bottom: 0;
    }

    .advantages-block-item {
        padding: 20px 10px 15px 10px;
    }

    .advantages-block-item p {
        margin-top: 10px;
    }

    .wark-block {
        padding-bottom: 30px;
    }

    .wark-block-item {
        height: unset;
        padding: 15px 10px;
    }

    .wark-block-item p {
        margin-top: 15px;
        margin-bottom: 10px;
    }

    .description-block img {
        width: calc(100% - 30px);
        box-sizing: border-box;
        margin-left: auto !important;
        margin-right: auto !important;
    }

    .form-block p {
        font-size: 9px;
        line-height: 1.3;
    }
}
.show-btn{
    &.hide{
        opacity: 0;
        pointer-events: none;
    }
}
