    #header {
        position: sticky;
        top: 0;
        height: auto;
        overflow: hidden;
        max-height: 200px;
       // transition: max-height .4s;
        .fix-container>.container {
            position: relative;
        }

        @media screen and (max-width:992px) {

            .fix-container {
                .menu-container {
                    display: none;
                }

                .header-links {
                    padding-top: 5px;
                    padding-bottom: 5px;

                    .col-lg {
                        &:nth-child(1) {
                            margin-top: 0;
                        }

                        &:nth-child(2) {
                            margin-top: 0;
                            padding-left: 0;

                            @media screen and (max-width:768px) {
                                display: none;
                            }
                        }

                        &:nth-child(3) {
                            display: none;
                            padding: 0;
                            text-align: center;
                        }

                        &:nth-child(4) {
                            display: none;
                            padding: 0;
                        }

                        &:nth-child(5) {
                            display: none;
                            padding: 0;
                        }
                    }

                    .nice-select {
                        padding-left: 0;

                        @media screen and (max-width:768px) {
                            margin-left: 0;
                        }
                    }
                }

                .burger {
                    position: absolute;
                    right: 0;
                    top: 20px;
                    width: 30px;
                    height: 30px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    padding: 5px;
                    border: 2px solid #000;
                    border-radius: 5px;
                    justify-content: space-between;
                    background: #fff;
                    cursor: pointer;

                    span {
                        width: 100%;
                        height: 2px;
                        display: block;
                        background-color: #000;
                        transition: all 0.5s;
                    }

                    &.active {
                        justify-content: center;

                        span {
                            &:nth-child(2) {
                                opacity: 0;
                            }

                            &:nth-child(1) {
                                transform: rotate(45deg);
                            }

                            &:nth-child(3) {
                                margin-top: -4px;
                                transform: rotate(-45deg);
                            }
                        }
                    }
                }
            }

            .fix-container.active {
                .header-links {
                    .col-lg {
                        &:nth-child(1) {
                            display: flex;
                            justify-content: left;
                        }

                        &:nth-child(2) {
                            display: flex;
                            justify-content: flex-start;
                            align-items: center;

                            .nice-select {
                                margin-left: 0;
                            }
                        }

                        &:nth-child(3) {
                            display: flex;
                            justify-content: flex-start;
                            align-items: center;

                            a {
                                justify-content: flex-start;
                                align-items: center;
                            }
                        }

                        &:nth-child(4) {
                            display: flex;

                            a {
                                width: 100%;
                                justify-content: flex-start;
                                align-items: center;
                            }
                        }

                        &:nth-child(5) {
                            display: flex;

                            a {
                                margin-left: 0;
                            }
                        }
                    }

                }

                .menu-container {
                    display: block;
                    padding: 10px 0;

                    .navbar-menu {
                        width: 100%;

                        .navbar-nav {
                            flex-direction: row;

                            .nav-item {
                                font-size: 14px;
                            }

                            .nav-link {
                                padding: 5px;
                            }

                            .drop-ul {
                                min-width: 200px;
                                width: auto;

                                a {
                                    padding: 5px !important;
                                }
                            }
                        }
                    }
                }
            }
        }

        @media screen and (max-width:576px) {
            .fix-container {
                .burger {
                    right: 15px;
                }

                .menu-container {
                    padding: 0;

                    .navbar-menu .navbar-nav {
                        flex-direction: column !important;

                        .drop-ul {
                            width: 100%;
                        }
                    }
                }
            }
        }

        .fix-container {
            position: relative;

            &:before {
                content: "";
                position: absolute;
                left: 0;
                top: 95px;
                width: 100%;
                height: 1px;
                display: block;
                background-color: #0e96ff;

                @media screen and (max-width:992px) {
                    display: none;
                }
            }

            &:after {
                content: "";
                position: absolute;
                left: 0;
                bottom: 0;
                width: 100%;
                height: 1px;
                display: block;
                background-color: #0e96ff;

                @media screen and (max-width:992px) {
                    display: none;
                }
            }
        }

        @media screen and (max-width:992px) {
            .navbar-toggler {
                margin-left: auto;
            }
        }

        z-index: 20;
        background-color: #fff;

        @media screen and (min-width:1200px) {
            .container {
                padding: 0;
            }
        }

        @media screen and (max-width:1200px) {
            .col-lg {
                margin-top: 10px;
            }
        }

        .header-links {
            align-items: center;
            justify-content: space-between;
            padding-top: 18px;
            padding-bottom: 18px;

            @media screen and (min-width:992px) {
                .col-lg {
                    &:nth-child(1) {
                        width: 181px;
                    }
                }
            }
        }

        .nice-select {
            text-align: center;
            display: block;
            width: 157px;
            margin: 0 auto;
            padding-right: 10px;
            font-size: 18px;
            font-weight: 600;
            font-family: 'Futura PT', sans-serif;
            background: 99% center / 10px no-repeat url(../img/keyboard-right-arrow-button.svg);
            color: #000;
            box-shadow: none;
            border: none;
            cursor: pointer;

            .option {
                min-height: 23px;
                font-size: 18px;
                line-height: 23px;
                font-weight: 600;
                font-family: 'Futura PT', sans-serif;
                text-align: center;
                background: transparent;
                color: #a4a4a4;

                &:hover {
                    color: #000;
                }
            }

            &:after {
                display: none;
            }

            .list {
                margin-top: -10px;
                background: transparent;
                box-shadow: none;
                background-color: #fff;
            }
        }

        .logo-link {
            justify-content: left;
            align-items: center;
            min-width: 181px;
            text-decoration: none;

            &:hover {
                color: #0987d4;
            }

            span {
                font-family: 'Circe', sans-serif;

                &:nth-child(1) {
                    font-size: 25px;
                    line-height: 29px;
                    font-weight: 900;
                    letter-spacing: -1px;
                    color: #0987d4;
                }

                &:nth-child(2) {
                    margin-top: 8px;
                    font-size: 16px;
                    line-height: 20px;
                    font-weight: 400;
                    color: #000;
                }
            }

            img {
                height: 59px;
            }
        }

        .phone-link {
            min-width: 188px;
        }

        .phone-link,
        .mail-link {
            justify-content: center;
            align-items: center;
            text-decoration: none;
            cursor: pointer;

            .d-flex {
                width: 31px;
                height: 31px;
                margin-right: 6px;
                align-items: center;
                justify-content: center;
                box-sizing: border-box;
                border: 3px solid #0e96ff;
                border-radius: 15.5px;
                transition: all 0.5s;
            }

            span {
                font-style: normal;
                font-size: 18px;
                font-weight: 600;
                font-family: 'Futura PT', sans-serif;
                color: #000;
            }

            &:hover {
                .d-flex {
                    background-color: #0e96ff;
                }

                path {
                    fill: #fff;
                }

                polygon {
                    fill: #fff;
                }
            }
        }

        .orange-button {
            width: 245px;
            height: 45px;

            @media screen and (max-width:1200px) {
                margin: 0 auto;
            }
        }

        .menu-container {
            @media screen and (max-width:992px) {
                .navbar {
                    .nav-item:hover {
                        .drop-ul {
                            width: 100%;
                        }
                    }
                }
            }

            nav {
                .navbar-menu {
                    width: 100%;
                }

                .navbar-nav {
                    width: 100%;
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                }

                padding: 0;

                li:not(.drop-ul-li) {
                    position: relative;
                    padding: 0px;
                    font-style: normal;
                    font-size: 20px;
                    font-weight: normal;
                    font-family: 'Circe', sans-serif;
                    border-radius: 0;
                    color: #000;

                    &.active {
                        a:not(.drop-a) {
                            color: #fff;
                            background-color: #0090ff;

                            &:hover {
                                color: #fff;
                            }
                        }
                    }

                    a {
                        padding: 11px 30px;

                        @media screen and (max-width:1200px) {
                            padding: 12px 30px;
                        }

                        color: #000;

                        &:hover {
                            color: #0090ff;
                        }
                    }

                    &:hover {
                        >.drop-ul {
                            transform: scale(1);
                        }
                    }
                }
            }

            .drop-ul {
                position: absolute;
                left: -50%;
                z-index: 9;
                display: block;
                width: 252px;
                border: 1px solid #0d96ff;
                box-sizing: border-box;
                list-style: none;
                transition: all 0.5s;
                transform: scale(0);
                transform-origin: top;

                &-li {
                    position: relative;
                    transition: all 0.5s;

                    a {
                        display: block;
                        width: 100%;
                        padding: 17px 10px !important;
                        text-align: center;
                        font-size: 16px;
                        background: #fff;
                        color: #000;
                        box-sizing: border-box;
                        text-decoration: none;
                        transition: all 0.5s;
                        cursor: pointer;

                        &:hover {
                            background: #0d96ff;
                            color: #fff !important;
                            text-decoration: none;
                        }
                    }
                }

                @media screen and (max-width:992px) {
                    left: 0;
                }
            }
        }
    }

    @media screen and (min-width:992px) {
        #header.header_min {
            max-height: 43px;
           // height: 43px;
            width: 100%;
            background-color: #fff;
            border-top: 1px solid #0e96ff;
            border-bottom: 1px solid #0e96ff;

            .fix-container {
                position: relative;
                height: 100%;
                max-width: 1140px;
                margin: 0 auto;

                &:before {
                    display: none;
                }
            }

            .container {
                height: 100%;
            }

            .row {
                height: 100%;

                .col-lg {
                    &:nth-child(1) {
                        position: absolute;
                        left: 0;
                        width: 138px;
                        height: 100%;
                        padding: 0;
                    }

                    &:nth-child(2) {
                        display: none;
                    }

                    &:nth-child(3) {
                        position: absolute;
                        right: 0;
                        width: 190px;
                        padding: 0;
                    }

                    &:nth-child(4) {
                        display: none;
                    }

                    &:nth-child(5) {
                        display: none;
                    }
                }
            }

            .menu-container {
                position: absolute;
                top: 0;
                left: 50%;
                height: 100%;
                max-width: 780px;
                margin-left: -400px;

                nav {
                    height: 100%;
                    margin: 0 auto;

                    .nav-item {
                        font-size: 14px;
                    }
                }

            }

            .fix-container:after {
                display: none;
            }

            .logo-link {
                width: 100%;
                height: 100%;
                justify-content: left;

                span {
                    &:nth-child(1) {
                        font-size: 12px;
                        line-height: 15px;
                        letter-spacing: 0px;
                    }

                    &:nth-child(2) {
                        margin-top: 0px;
                        font-size: 8px;
                        line-height: 11px;
                    }
                }

                img {
                    width: 30px;
                    height: 29px;
                }
            }

            .phone-link {
                width: 100%;
                max-width: none;
                justify-content: flex-end;
            }
        }

        .navbar-collapse {
            height: 100%;

            .navbar-nav {
                height: 100%;

                .nav-item {
                    height: 100%;

                    a {
                        height: 100%;
                    }
                }
            }
        }
    }

    @media screen and (max-width:1200px) and (min-width:992px) {
        #header .col-lg {
            margin-top: 0;
        }

        #header.header_min .logo-link {
            justify-content: center;
        }

        #header.header_min .phone-link span {
            font-size: 14px;
        }

        #header.header_min .menu-container {
            max-width: 666px;
            margin-left: -333px;
        }

        #header.header_min .menu-container nav .nav-item {
            font-size: 12px;
            ;
        }
    }

    @media screen and (max-width:992px) {
        #header {
            position: fixed !important;
            width: 100%;
            border-top: 1px solid #0e96ff;
            border-bottom: 1px solid #0e96ff;

            &+* {
                margin-top: 71px;
            }
        }

        #header .col-lg {
            &:nth-child(1) {
                padding: 0;
            }

            .menu-container {
                .navbar {
                    top: 53px;
                }
            }
        }

        #header.header_min {
            position: sticky;
            height: auto;
            width: 100%;
            background-color: #fff;
        }
    }

    @media screen and (max-width:768px) {
        #header {
            .col-lg {
                &:nth-child(3) {
                    display: none;
                }
            }

            .header-links {
                padding: 15px;
            }

            .logo-link {
                justify-content: left;
            }
        }
    }

    @media screen and (max-width:1200px) and (min-width:992px) {

        #header .mail-link span,
        #header .phone-link span {
            font-size: 14px;
        }

        #header .nice-select {
            font-size: 14px;
        }

        #header .orange-button {
            width: 200px;
            font-size: 14px;
        }

        #header .nice-select {
            width: 130px;
        }

        #header .logo-link {
            width: 100%;
            min-width: auto;
            justify-content: space-between;

            span {
                &:nth-child(1) {
                    font-size: 14px;
                    font-weight: 600;
                }

                &:nth-child(2) {
                    margin-top: 0;
                    font-size: 12px;
                }
            }

            img {
                width: 40px;
            }
        }

        #header .header-links .col-lg:nth-child(1) {
            width: auto;
            padding: 0;
        }
    }
